:root {
  --main-background: #eae7dc;
  --light-pink: #e97e72;
  --gray: #8b8484;
  --darkGray: #474747;
  --beige: #d8c3a5;
  --light-beige: #e3d9cb;
  --dark-pink: #e85a4f;
  --margin: 3rem;
}

body {
  background-color: var(--main-background);
  color: var(--darkGray);
  flex-direction: column;
  min-height: 100vh;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  display: flex;
}

nav {
  margin-top: 1rem;
  margin-right: 5rem;
}

a {
  color: var(--gray);
  text-decoration: none;
  transition: color .2s ease-in-out;
}

a:hover, a:active {
  color: var(--light-pink);
  font-family: Cascadia Code, Consolas, Courier New;
}

.ulNav {
  word-spacing: 1rem;
  justify-content: right;
  margin-bottom: 1rem;
  list-style-type: none;
  display: grid;
}

@media (width <= 1024px) {
  .container {
    grid-template-columns: 1fr;
  }
}

ul {
  list-style: none;
}

.main-content {
  grid-template-rows: auto 1fr auto;
  width: 100%;
  padding: 3px;
  display: grid;
}

img {
  object-fit: contain;
  width: 100%;
  height: auto;
}

p {
  color: var(--gray);
  text-align: center;
  margin-left: var(--margin);
  margin-right: var(--margin);
}

.myWork {
  background-color: var(--main-background);
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  justify-content: center;
  align-items: center;
  display: grid;
}

.portfolioHeaderDiv {
  flex-direction: column;
  align-content: center;
  width: 15rem;
  height: 2rem;
  display: flex;
}

.headerText {
  color: var(--darkGray);
  margin: auto;
  font-family: Courier New, Courier, monospace;
  font-size: 20px;
}

.portfolioItems {
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
  place-content: center space-evenly;
  width: 100%;
  height: 100%;
  padding: 10px;
  transition: box-shadow .3s;
}

.portfolioItems:hover {
  box-shadow: 0 4px 12px #5d5e5e4d;
}

.projectHeaderStart {
  color: var(--darkGray);
  text-align: center;
  height: 2rem;
  margin: 1rem;
  font-family: Courier New, Courier, monospace;
}

.textHome {
  text-align: center;
  color: var(--light-pink);
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 3rem;
}

.textHome2 {
  text-align: center;
  color: var(--light-pink);
  margin-bottom: 5rem;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 2rem;
}

.contactDiv {
  flex-wrap: wrap;
  grid-column: auto;
  grid-auto-flow: column;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  margin: 2rem auto;
  display: grid;
}

.profileImg {
  border-radius: 50%;
  width: 100%;
  height: auto;
  transition: width .4s;
  display: grid;
}

.profileImg:hover {
  width: 150%;
}

.contactText {
  color: var(--gray);
  text-align: left;
}

@media (width <= 768px) {
  .contactDiv {
    text-align: center;
    flex-direction: column;
    display: flex;
  }

  .profileImg {
    width: 80%;
  }
}

.wrapDivs {
  margin-bottom: var(--margin);
  background-color: #fff;
  display: grid;
}

.projectPhotos {
  place-content: center space-evenly;
  width: 100%;
  height: 100%;
  transition: transform .2s;
}

.projectPhotos:hover {
  transform: scale(1.5);
}

.projectDescriptionDiv {
  background-color: #fff;
  flex-direction: column;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  margin-top: 1rem;
  display: grid;
}

@media (width <= 768px) {
  .projectDescriptionDiv {
    grid-template-columns: 1fr;
  }

  .projectContent {
    flex-direction: column;
  }

  .portfolioItems {
    width: 100%;
  }
}

.projectHeaderDiv {
  background-color: var(--light-beige);
  flex-direction: column;
  align-self: flex-start;
  width: 20rem;
  height: 3rem;
  display: flex;
}

.portfolioItems-img {
  width: 100%;
}

.projectHeaderText {
  color: var(--darkGray);
  justify-content: left;
  align-items: center;
  margin: auto;
  font-family: Courier New, Courier, monospace;
  font-size: 20px;
  display: flex;
}

.projectDescriptionText {
  margin: 2rem;
}

.projectImgDiv {
  grid-template-columns: 1fr 1fr 1fr 1fr;
  width: 100%;
  display: grid;
}

footer {
  margin-top: var(--margin);
  grid-row-end: auto;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.footerText {
  color: var(--gray);
  text-align: center;
  justify-content: center;
  padding: 1rem;
  list-style: none;
  display: grid;
}

.footer-name {
  font-size: large;
}

.footer-email {
  color: var(--darkGray);
}

.footer-location {
  color: var(--light-pink);
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: var(--darkGray);
    color: #fff;
  }
}

@media (prefers-color-scheme: light) {
  body {
    background-color: var(--main-background);
    color: var(--darkGray);
  }
}
